<script>
import vueScroll from 'vue-scroll'
export default {
  data () {
    return {
      scrollEvt: {
        isBlog: false
      }
    }
  },
  components: {vueScroll},
  methods: {
    onScroll (e, position) {
      if (!this.scrollEvt.isBlog) {
        if (position.scrollTop >= 600) {
          this.scrollEvt.isBlog = true
        }
      }
      this.position = position
    }
  }
}
